import kleur from "kleur"
import Logger from "../../helpers/Logger.js"

const createHandle = (exceptions, srv) => {

    return (exception, handler) => {
        if(exceptions[exception]) return Logger(`Handler for exception (${kleur.blue().bold(exception)}) in service (name: ${kleur.blue().bold(srv.name)}) has already been declared`, 'service')

        exceptions[exception] = handler
    }

}

export default createHandle